// 简单的 store 模式
import Vue from "vue";

export default new Vue({
  data() {
    return {
      game: {
        wordList: [],
        currentWord: "",
        playing: false,
      },
      boardWindow: "rules",

      flippedWord: null,

      inputIsChecked: true,

      score: {
        currentScore: null,
      },
      defaultConfig: {
        //初始化获取，后期从本地存储读取
        audio: "ukphone", //ukphone(英) or usphone(美)
        dictId: "DEMO_1",
      },

      showDialog: false,
    };
  },
});
///需要注意，所有 store 中 state 的变更，都放置在 store 自身的 action 中去管理。
///这种集中式状态管理能够被更容易地理解哪种类型的变更将会发生，以及它们是如何被触发。
///当错误出现时，我们现在也会有一个 log 记录 bug 之前发生了什么`
